
//
// Chart
// --------------------------------------------------

$color-status-list-text: #7e8c8d

$color-chart-tooltip-bg: #000000
$color-chart-tooltip-text: #ffffff

$color-scoreboard-title: $color-grey-1
$color-scoreboard-description: $color-grey-2
$color-scoreboard-body: $color-grey-1
$color-scoreboard-icon: #e5a91a
$color-scoreboard-positive: #95b753
$color-scoreboard-negative: #cc3300

=control-chart-non-centered
  text-align: left

  .canvas
    margin-right: 20px
    display: inline-block

//
// Chart
// --------------------------------------------------

.control-chart
  div.canvas
    display: inline-block
    margin-right: 20px
    margin-bottom: 20px
    position: relative

    span.center
      position: absolute
      display: block
      text-align: center
      width: 100%
      top: 50%
      margin-top: -21px
      font-size: 30px
      font-weight: 100
      color: $color-scoreboard-title
      z-index: $zindex-chart - 1

    svg
      z-index: $zindex-chart

  &.full-width div.canvas
    margin-right: 0 !important

  ul
    display: inline-block
    height: inherit
    margin: 0
    padding: 0
    list-style: none
    position: relative
    vertical-align: top

    li
      width: 120px
      white-space: normal
      display: block
      text-transform: uppercase
      color: $color-scoreboard-title
      font-weight: 300
      font-size: 12px
      margin-bottom: 10px

      span
        float: right
        font-weight: 600

      &:last-child
        margin-bottom: 0

  div.chart-legend
    display: inline-block
    vertical-align: top
    text-align: left

    table
      font-size: 12px
      color: $color-scoreboard-title

      tr
        td
          padding: 0 0 7px 0
          vertical-align: top

          &.value
            padding-left: 10px
            font-weight: 600

          i
            display: inline-block
            width: 13px
            height: 13px

            +border-radius($border-radius-base)

            text-indent: -100000em
            margin-right: 5px
            position: relative
            top: 2px

          &.indicator
            width: 20px

        &:last-child td
          padding-bottom: 0

  +control-chart-non-centered

  &.centered
    text-align: center

    .canvas
      margin-right: 0
      display: block
      margin-left: auto
      margin-right: auto

  &.wrap-legend div.chart-legend table tr
    display: inline-block
    white-space: nowrap
    margin-right: 20px

    &:last-child td
      padding-bottom: 7px

.report-container .wrapped .control-chart
  +control-chart-non-centered

#flotTip, #chart-tooltip
  white-space: nowrap
  padding: 7px 10px
  background: $color-chart-tooltip-bg
  position: absolute
  z-index: $zindex-tooltip
  color: $color-chart-tooltip-text
  border-radius: $border-radius-base * 2
  font-size: 12px
  opacity: .8

.title-value
  h4
    font-size: 12px
    text-transform: uppercase
    color: $color-scoreboard-title
    margin: 0

  span.goal-meter-indicator
    float: left
    height: 24px
    width: 10px
    margin-right: 5px
    position: relative
    top: 9px
    background: $color-scoreboard-negative

    > span
      text-indent: -10000em
      display: block
      position: absolute
      width: 10px
      left: 0
      bottom: 0
      background: $color-scoreboard-positive
      height: 0

      +transition(all 0.2s)

  &.goal-meter-inverse
    span.goal-meter-indicator
      background: $color-scoreboard-positive

      > span
        background: $color-scoreboard-negative

  p
    color: $color-scoreboard-body
    margin: 0
    font-size: 28px
    line-height: 41px

    &:before
      color: $color-scoreboard-icon
      font-size: 22px

    &.success
      color: $color-scoreboard-positive

    &.danger
      color: $color-scoreboard-negative

    &.negative, &.positive
      &:after
        font-size: 17px
        vertical-align: top
        position: relative
        top: -3px
        left: 5px

    &.negative
      color: $color-scoreboard-negative

      &:after
        +icon($angle-double-down)

    &.positive
      color: $color-scoreboard-positive

      &:after
        +icon($angle-double-up)

    &.description
      color: $color-scoreboard-description
      font-weight: 300
      line-height: 100%
      font-size: 13px

.report-container
  .title-value
    margin-top: -18px

    p
      font-weight: 100
      font-size: 40px

      &.description
        font-size: 12px
        margin-top: 9px

      &:before
        font-size: 30px
        margin-right: 10px

      &.negative, &.positive
        &:after
          top: -8px

    span.goal-meter-indicator
      height: 31px
      top: 4px
      width: 15px
      margin-right: 10px

      span
        width: 15px

//
// Status list
// --------------------------------------------------

.control-status-list > ul
  margin-bottom: 0
  padding: 0

  li
    margin: 0
    padding: 7px 15px 6px
    list-style: none
    display: block
    font-size: $font-size-base - 1
    color: $color-status-list-text
    border-bottom: 1px solid #f0f0f0

    &:last-child
      border-bottom: none

    a
      color: $color-status-list-text
      text-decoration: none

      &:hover
        color: $link-color
        text-decoration: none

    .status-text
      margin: 0 5px

      &.muted
        color: $text-muted

      &.primary
        +text-emphasis-variant($brand-primary)

      &.success
        +text-emphasis-variant($state-success-text)

      &.info
        +text-emphasis-variant($state-info-text)

      &.warning
        +text-emphasis-variant($state-warning-text)

      &.danger
        +text-emphasis-variant($state-danger-text)

    .status-label
      float: right
      display: inline-block
      padding: 1px 5px

      +border-radius($border-radius-base)

      &:not(.link)
        color: white
        letter-spacing: 1px
        font-family: $font-family-monospace

    .status-icon
      display: inline-block
      text-align: center
      color: white
      width: 22px
      height: 22px
      position: relative
      top: -1px

      +border-radius(100px)

      > i
        font-size: 10px
        line-height: 22px

    .status-icon,
    .status-label
      background: #aaa

      &.success
        background: $brand-success

      &.primary
        background: $brand-primary

      &.warning
        background: $brand-warning

      &.danger
        background: $brand-danger

      &.info
        background: $brand-info

      &.link
        background: transparent

.gecko .control-status-list > ul li span.status.circle
  top: -2px

.report-container
  .control-status-list > ul
    margin: -15px
