//
// Tooltips
// --------------------------------------------------

$tooltip-max-width: 200px
$tooltip-color: #fff
$tooltip-bg: #34495e
$tooltip-opacity: 0.9

$tooltip-arrow-width: 5px
$tooltip-arrow-color: $tooltip-bg
$tooltip-box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 2px 4px rgba(0, 0, 0, 0.23)
