

//
// Flash Messages
// --------------------------------------------------

$color-flash-success-bg: $brand-success
$color-flash-error-bg: #cc3300
$color-flash-warning-bg: #f0ad4e
$color-flash-info-bg: #5fb6f5
$color-flash-text: #ffffff

#layout-canvas
  .flash-message
    display: none

.flash-message
  position: fixed
  width: 500px
  left: 50%
  top: 50px
  margin-left: -250px
  color: $color-flash-text
  font-size: 14px
  padding: 10px 30px 10px 15px
  z-index: $zindex-flashmessage
  word-wrap: break-word
  text-align: left
  box-shadow: $box-shadow-z1

  +border-radius($border-radius-base)

  &.fade
    opacity: 0

    transition: all 0.5s, width 0s
    transform: scale(0.9)

  &.fade.in
    opacity: 1

    transform: scale(1)

  &.success
    background: $color-flash-success-bg

  &.error
    background: $color-flash-error-bg

  &.warning
    background: $color-flash-warning-bg

  &.info
    background: $color-flash-info-bg

  button
    float: none
    position: absolute
    right: 10px
    top: 12px
    color: white

    +hide-text

    opacity: 1
    outline: none

    &:before
      +icon-OctoFont

      content: $octo-icon-cross
      color: white
      font-size: 16px
      position: relative

    &:hover
      opacity: 1

  &.static
    position: static !important
    width: auto !important
    display: block !important
    margin-left: 0 !important
    box-shadow: none

@media (max-width: $screen-sm)
  .flash-message
    left: 10px
    right: 10px
    top: 10px
    margin-left: 0
    width: auto
