

$color-scroll-indicator: $color-grey-2

$color-tab-border: $color-grey-3
$color-tab-inactive-text: $color-grey-2
$color-tab-active-text: $color-grey-1
$color-tab-hover-text: $color-grey-1
$color-tab-active-border: $color-grey-3
$color-tab-bg: #ffffff
$color-tab-active-marker: #ec8017
$color-tab-content-active-bg: #ffffff
$color-tab-content-border: #e3e5e7

$color-tab-primary-border: #DCE1E7
$color-tab-primary-color: $color-grey-2
$color-tab-primary-active-color: $color-grey-1

$nav-link-padding: 10px 15px
$nav-link-hover-bg: $gray-lighter

$nav-disabled-link-color: $gray-light
$nav-disabled-link-hover-color: $gray-light

$nav-open-link-hover-color: #fff

$nav-tabs-border-color: #ddd

$nav-tabs-link-hover-border-color: $gray-lighter

$nav-tabs-active-link-hover-bg: $body-bg
$nav-tabs-active-link-hover-color: $gray
$nav-tabs-active-link-hover-border-color: #ddd

$nav-tabs-justified-link-border-color: #ddd
$nav-tabs-justified-active-link-border-color: $body-bg

$nav-pills-border-radius: $border-radius-base
$nav-pills-active-link-hover-bg: $component-active-bg
$nav-pills-active-link-hover-color: $component-active-color


//
// Navs
// --------------------------------------------------

// Base class
// --------------------------------------------------

.nav
  margin-bottom: 0
  padding-left: 0

  // Override default ul/ol
  list-style: none

  @extend .clearfix

  > li
    position: relative
    display: block

    > a
      position: relative
      display: block
      padding: $nav-link-padding

      &:hover,
      &:focus
        text-decoration: none
        background-color: $nav-link-hover-bg

    // Disabled state sets text to gray and nukes hover/tab effects
    &.disabled > a
      color: $nav-disabled-link-color

      &:hover,
      &:focus
        color: $nav-disabled-link-hover-color
        text-decoration: none
        background-color: transparent
        cursor: not-allowed

  // Open dropdowns
  .open > a
    &,
    &:hover,
    &:focus
      background-color: $nav-link-hover-bg
      border-color: $link-color

// Tabs
// -------------------------

// Give the tabs something to sit on
.nav-tabs
  border-bottom: 1px solid $nav-tabs-border-color

  > li
    float: left

    // Make the list-items overlay the bottom border
    margin-bottom: -1px

    // Actual tabs (as links)
    > a
      margin-right: 2px
      line-height: $line-height-base

      //border: 1px solid transparent;
      border-radius: $border-radius-base $border-radius-base 0 0

      &:hover
        border-color: $nav-tabs-link-hover-border-color $nav-tabs-link-hover-border-color $nav-tabs-border-color

    // Active state, and its :hover to override normal :hover
    &.active > a
      &,
      &:hover,
      &:focus
        color: $nav-tabs-active-link-hover-color
        background-color: transparent

        //border: 1px solid $nav-tabs-active-link-hover-border-color;
        border-bottom-color: transparent
        cursor: default

  // pulling this in mainly for less shorthand
  &.nav-justified
    @extend .nav-justified
    @extend .nav-tabs-justified

// Pills
// -------------------------
.nav-pills
  > li
    float: left

    // Links rendered as pills
    > a
      +border-radius($nav-pills-border-radius)

    + li
      margin-left: 2px

    // Active state
    &.active > a
      &,
      &:hover,
      &:focus
        color: $nav-pills-active-link-hover-color
        background-color: $nav-pills-active-link-hover-bg

// Stacked pills
.nav-stacked
  > li
    float: none

    + li
      margin-top: 2px
      margin-left: 0

      // no need for this gap between nav items

// Nav variations
// --------------------------------------------------

// Justified nav links
// -------------------------

.nav-justified
  width: 100%

  > li
    float: none

    > a
      text-align: center
      margin-bottom: 5px

  > .dropdown .dropdown-menu
    top: auto
    left: auto

  @media (min-width: $screen-sm-min)
    > li
      display: table-cell
      width: 1%

      > a
        margin-bottom: 0

// Move borders to anchors instead of bottom of list
//
// Mixin for adding on top the shared `.nav-justified` styles for our tabs
.nav-tabs-justified
  border-bottom: 0

  > li > a
    // Override margin from .nav-tabs
    margin-right: 0
    border-radius: $border-radius-base

  > .active > a,
  > .active > a:hover,
  > .active > a:focus
    border: 1px solid $nav-tabs-justified-link-border-color

  @media (min-width: $screen-sm-min)
    > li > a
      border-bottom: 1px solid $nav-tabs-justified-link-border-color
      border-radius: $border-radius-base $border-radius-base 0 0

    > .active > a,
    > .active > a:hover,
    > .active > a:focus
      border-bottom-color: $nav-tabs-justified-active-link-border-color

// Tabbable tabs
// -------------------------

// Hide tabbable panes to start, show them when `.active`
.tab-content
  > .tab-pane
    display: none

  > .active
    display: block

// Dropdowns
// -------------------------

// Specific dropdowns
.nav-tabs .dropdown-menu
  // make dropdown border overlap tab border
  margin-top: -1px

  // Remove the top rounded corners here since there is a hard edge above the menu
  +border-top-radius(0)

.control-tabs
  position: relative
  margin-bottom: 20px

  &:last-child
    margin-bottom: 0

  +horizontal-scroll-indicators($color-scroll-indicator)

  &.scroll-active-before:before
    color: $color-tab-active-border

  &.scroll-active-after:after
    color: $color-tab-active-border

  &:before, &:after
    top: 10px
    margin-top: 0

  > ul.nav-tabs, > div > ul.nav-tabs, > div > div > ul.nav-tabs
    white-space: nowrap
    font-size: 0
    overflow: hidden
    border-bottom: none
    vertical-align: bottom

    > li
      font-size: 15px
      float: none
      display: inline-block
      vertical-align: bottom
      margin-right: 20px
      position: relative
      z-index: $zindex-tab - 2

      &:last-child
        margin-right: 0

      a
        // Commented out in base
        // border-left: none !important;
        // border-top: none !important;
        // border-right: none !important;
        padding: 0 0 10px 0
        color: $color-tab-inactive-text
        font-weight: 400
        overflow: hidden

        &:hover
          background-color: transparent
          border-bottom-color: transparent

        &:before
          font-size: 14px

        > span.title > span
          max-width: 150px
          overflow: hidden
          text-overflow: ellipsis
          display: inline-block
          margin-top: -4px
          padding-top: 7px

          > span:not([class*="oc-icon"])
            margin-right: 8px

      span.tab-close
        display: none

      &.active
        z-index: $zindex-tab

        a
          color: $color-tab-active-text

        &:after
          width: 100%
          height: 3px
          bottom: 0
          background: $color-tab-active-border

  > div.tab-content
    > div.tab-pane
      padding: $padding-standard 0 0 0
      display: none

      +clearfix

      &.active
        display: block

        &.layout-cell
          display: table-cell

      &.pane-compact
        padding: 0

      &.pane-padded
        padding: 20px 20px 0 20px

      &.pane-bordered
        border-left: 1px solid $color-tab-border
        border-right: 1px solid $color-tab-border
        border-bottom: 1px solid $color-tab-border

  &[data-closable]
    > ul.nav-tabs, > div > ul.nav-tabs, > div > div > ul.nav-tabs
      > li
        margin-right: 5px

        a
          padding-left: 20px !important
          padding-right: 0 !important

        span.tab-close
          display: block
          position: absolute
          width: 20px
          height: 20px
          top: 5px
          left: -5px
          text-align: right
          font-size: 12px
          cursor: pointer
          color: $color-tab-inactive-text

          i
            display: inline-block
            z-index: $zindex-tab + 1
            top: -7px
            right: 5px
            position: relative

          &:hover
            color: $brand-danger

        &.active span.close
          color: $color-tab-inactive-text

        &[data-modified]
          span.tab-close i
            top: -4px

            &:before
              content: $circle
              font-size: 9px

  //
  // Master tabs
  //

  &.master-tabs
    > ul.nav-tabs, > div > ul.nav-tabs, > div > div > ul.nav-tabs
      > li
        a
          font-size: 15px
          border-bottom: transparent 4px solid
          position: relative
          z-index: $zindex-tab + 1
          line-height: 100%

        &.active
          a
            border-bottom: $color-tab-active-border 4px solid

  //
  // Primary tabs
  //

  &.primary-tabs
    margin-bottom: 5px

    > ul.nav-tabs, > div > ul.nav-tabs, > div > div > ul.nav-tabs
      position: relative
      margin-left: 0
      margin-right: 0

      &:before
        position: absolute
        bottom: 0
        height: 1px
        width: 100%
        z-index: $zindex-tab - 1
        content: ' '
        border-bottom: 2px solid $color-tab-border

      > li
        padding-right: 0
        padding-left: 0
        margin-left: 0
        margin-right: -10px
        background: transparent

        &:first-child
          padding-left: $padding-standard !important

        &:last-child
          margin-right: 0

        a
          font-size: $font-size-base
          font-weight: 500
          padding-bottom: 3px
          margin: 0
          position: relative
          top: -1px
          z-index: $zindex-tab + 1
          background: transparent
          overflow: visible
          border-bottom: 1px solid transparent

          > span.title
            position: relative
            display: inline-block
            padding: 4px 25px 0px

            +box-sizing(border-box)

            z-index: $zindex-tab

            > span
              border-top: 2px solid transparent

            &:before, &:after
              content: ''
              display: none
              border-top: 2px solid $color-tab-border
              position: absolute
              background: transparent
              top: 0
              z-index: -1
              width: 20px
              bottom: -2px
              transform-origin: bottom

            &:before
              left: 0
              border-left: 2px solid $color-tab-border

              +border-radius(8px 0 0 0)
              transform: skewX(-10deg)

            &:after
              right: 0
              border-right: 2px solid $color-tab-border

              +border-radius(0 8px 0 0)
              transform: skewX(10deg)

        &:hover
          a, a > span.title
            color: $color-tab-hover-text

        &:last-child
          background-image: none
          margin-right: 0
          padding-right: 5px

        &:first-child
          padding-left: 0

        &.active a
          z-index: $zindex-tab + 3
          top: 0

          > span.title
            z-index: $zindex-tab + 2
            border-top-color: #d6d6d6

            &:before, &:after
              display: block
              border-color: $color-tab-active-border

            span
              border-top-color: $color-tab-active-border

          &:before
            position: absolute
            bottom: 0
            height: 2px
            right: 2px
            left: 2px
            content: ' '
            background-color: $body-bg

    // Tab divider to sit inset the standard padding (20px)
    &.tabs-inset
      > ul.nav-tabs, > div > ul.nav-tabs, > div > div > ul.nav-tabs
        margin-left: -20px
        margin-right: -20px

  //
  // Secondary tabs
  //

  &.secondary-tabs
    > ul.nav-tabs, > div > ul.nav-tabs, > div > div > ul.nav-tabs
      > li
        padding-right: 10px
        margin-right: 10px
        border-right: 1px solid $color-tab-border

        a
          font-size: 14px
          font-weight: 500
          padding-bottom: 0

        &:last-child
          border-right: none
          margin-right: 0
          padding-right: 0

    > div.tab-content
      > div.tab-pane
        padding-top: 15px

  //
  // Pill Tabs
  //

  &.pill-tabs
    margin-bottom: 5px

    > ul.nav-tabs, > div > ul.nav-tabs, > div > div > ul.nav-tabs
      position: relative

      > li
        padding: 0
        margin: 0
        background: transparent
        border: 1px solid $color-tab-primary-border

        + li
          margin-left: -1px

        a
          position: relative
          color: $color-tab-primary-color
          font-size: $font-size-base
          font-weight: 500
          padding-bottom: 0
          margin: 0
          z-index: $zindex-tab + 1
          background: transparent
          overflow: visible

          > span.title
            position: relative
            display: inline-block
            padding: 9px 25px 4px 25px

            +box-sizing(border-box)

            z-index: $zindex-tab

            > span
              margin: 0
              padding: 0
              border: none

        &:last-child
          background-image: none
          margin-right: 0
          padding-right: 5px
          border-top-right-radius: $border-radius-large
          border-bottom-right-radius: $border-radius-large

        &:first-child
          padding-left: 0
          border-top-left-radius: $border-radius-large
          border-bottom-left-radius: $border-radius-large

        &.active
          border: 1px solid $brand-primary

          a
            z-index: $zindex-tab + 3
            color: $color-tab-primary-active-color

            > span.title
              z-index: $zindex-tab + 2
              border-top-color: #d6d6d6

            &:before
              display: none

  //
  // Auxiliary tabs
  //

  &.auxiliary-tabs
    background: white

    =border-top($color)
      content: ' '
      display: block
      position: absolute
      width: 100%
      height: 1px
      background: $color
      top: 0
      left: 0

    > ul.nav-tabs, > div > ul.nav-tabs
      padding-left: 20px
      padding-bottom: 2px
      background: white
      position: relative

      &:before
        +border-top(#95a5a6)

      > li
        margin-right: 2px

        > a
          background: white
          color: #bdc3c7
          border-left: 1px solid #ecf0f1 !important
          border-right: 1px solid #ecf0f1 !important
          border-bottom: 1px solid #ecf0f1 !important
          padding: 4px 10px
          line-height: 100%

          +border-radius(0 0 4px 4px)

          > span.title > span
            margin-bottom: 0
            font-size: 13px
            height: auto

        &.active
          top: 0

          &:before
            +border-top(white)

            top: -1px

          a
            padding-top: 5px
            border-left: 1px solid #95a5a6 !important
            border-right: 1px solid #95a5a6 !important
            border-bottom: 1px solid #95a5a6 !important
            color: #95a5a6

    > div.tab-content > .tab-pane
      background: white

  //
  // Content tabs
  //

  &.content-tabs
    > ul.nav-tabs
      position: relative

      &:before
        content: ' '
        position: absolute
        bottom: 0
        left: 0
        width: 100%
        height: 1px
        background: $color-tab-content-border

      li
        margin-right: 0
        border-top: 1px solid $color-tab-content-border
        border-right: 1px solid $color-tab-content-border

        a
          font-size: 12px
          padding: 11px 20px
          margin: 0
          border: none
          text-transform: uppercase
          background: transparent
          font-weight: 500

          span.title
            span
              border-top: none

        &:first-child
          border-left: 1px solid $color-tab-content-border

        &:last-child
          border-right: 1px solid $color-tab-content-border

        &.active
          background: $color-tab-content-active-bg

          a
            font-weight: 600

    // Tab divider to sit inset the standard padding (20px)
    &.tabs-inset
      > ul.nav-tabs, > div > ul.nav-tabs, > div > div > ul.nav-tabs
        margin-left: -20px
        margin-right: -20px

        li:first-child
          margin-left: 20px

    // Tabs to sit in by the standard offset (20px)
    &.tabs-offset
      > ul.nav-tabs
        li:first-child
          margin-left: 20px

    // Tabs to sit flush to the element above
    &.tabs-flush
      > ul.nav-tabs
        li
          border-top: none

        li:last-child
          border-right: 1px solid transparent

        li:first-child
          border-left: 1px solid transparent

        li.active:last-child
          border-right: 1px solid $color-tab-content-border

    > .tab-content > .tab-pane
      padding-top: 0

      div.list-header,
      div.padded-container,
      div.toolbar-widget
        background: $color-tab-content-active-bg
        padding-top: 20px

      // Pane to sit inset the standard padding (20px)
      &.pane-inset
        margin-left: -20px
        margin-right: -20px

.hide-tabs
  .control-tabs
    ul.nav-tabs
      display: none

    > div.tab-content > div.tab-pane
      padding-top: 0

.form-sidebar
  .control-tabs.secondary
    > div > ul.nav-tabs > li a
      background: transparent
