html.cssanimations .cursor-loading-indicator {
  background: transparent url("/modules/system/assets/ui/images/loader-transparent.svg") no-repeat 50% 50%;
  animation: spin 1s linear infinite;
  background-size: 20px 20px;
  position: fixed;
  width: 20px;
  height: 20px;
}
html.cssanimations .cursor-loading-indicator.hide {
  display: none;
}

.bar-loading-indicator {
  transition: opacity 0.4s linear;
}
.bar-loading-indicator .progress-bar {
  animation: infinite-loader 90s ease-in forwards;
  transition-duration: 0s;
}
.bar-loading-indicator.bar-loaded {
  opacity: 0;
  transition-delay: 0.3s;
}
.bar-loading-indicator.bar-loaded .progress-bar {
  width: 100% !important;
  transition: width 0.3s linear;
  animation: none;
}

.stripe-loading-indicator {
  height: 4px;
  background: transparent;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  overflow: hidden;
  z-index: 10300;
}
.stripe-loading-indicator .stripe, .stripe-loading-indicator .stripe-loaded {
  height: 4px;
  display: block;
  background: #3498db;
  position: absolute;
  box-shadow: inset 0 1px 1px -1px #FFF, inset 0 -1px 1px -1px #FFF;
}
.stripe-loading-indicator .stripe {
  width: 100%;
  animation: infinite-loader 60s linear;
}
.stripe-loading-indicator .stripe-loaded {
  width: 0;
  opacity: 0;
}
.stripe-loading-indicator.loaded {
  opacity: 0;
  transition: opacity 0.4s linear;
  transition-delay: 0.3s;
}
.stripe-loading-indicator.loaded .stripe-loaded {
  opacity: 1;
  transition: width 0.3s linear;
  width: 100% !important;
}
.stripe-loading-indicator.hide {
  display: none;
}

@keyframes infinite-loader {
  0% {
    width: 0%;
  }
  10% {
    width: 42%;
  }
  20% {
    width: 63%;
  }
  30% {
    width: 78.75%;
  }
  40% {
    width: 88.59375%;
  }
  50% {
    width: 94.13085938%;
  }
  60% {
    width: 97.07244873%;
  }
  70% {
    width: 98.58920574%;
  }
  80% {
    width: 99.35943391%;
  }
  90% {
    width: 99.7475567%;
  }
  100% {
    width: 99.94237615%;
  }
}
@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(359deg);
  }
}
@keyframes rspin {
  0% {
    transform: rotate(359deg);
  }
  100% {
    transform: rotate(0deg);
  }
