//
// Clock Picker
//

.clockpicker .input-group-addon
  cursor: pointer

.clockpicker-moving
  cursor: move

.clockpicker-align-left.popover > .arrow
  left: 25px

.clockpicker-align-top.popover > .arrow
  top: 17px

.clockpicker-align-right.popover > .arrow
  left: auto
  right: 25px

.clockpicker-align-bottom.popover > .arrow
  top: auto
  bottom: 6px

.clockpicker-popover
  .popover-title
    background-color: #fff
    color: #999
    font-size: 24px
    font-weight: bold
    line-height: 30px
    text-align: center

    span
      cursor: pointer

  .popover-content
    background-color: #f8f8f8
    padding: 12px

    &:last-child
      border-bottom-left-radius: 5px
      border-bottom-right-radius: 5px

.clockpicker-plate
  background-color: #fff
  border: 1px solid #ccc
  border-radius: 50%
  width: 200px
  height: 200px
  overflow: visible
  position: relative

  // Disable text selection highlighting. Thanks to Hermanya
  -webkit-touch-callout: none
  -webkit-user-select: none
  -khtml-user-select: none
  -moz-user-select: none
  -ms-user-select: none
  user-select: none

.clockpicker-canvas,
.clockpicker-dial
  width: 200px
  height: 200px
  position: absolute
  left: -1px
  top: -1px

.clockpicker-minutes
  visibility: hidden

.clockpicker-tick
  border-radius: 50%
  color: #666
  line-height: 26px
  text-align: center
  width: 26px
  height: 26px
  position: absolute
  cursor: pointer

  &.active,
  &:hover
    background-color: rgb(192, 229, 247)
    background-color: rgba(0, 149, 221, 0.25)

