.clockpicker-dial
  +transition(unquote("transform 350ms, opacity 350ms"))

.clockpicker-dial-out
  opacity: 0

.clockpicker-hours.clockpicker-dial-out
  +transform(scale(1.2, 1.2))

.clockpicker-minutes.clockpicker-dial-out
  +transform(scale(0.8, 0.8))

.clockpicker-canvas
  +transition(opacity 175ms)

.clockpicker-canvas-out
  opacity: 0.25

.clockpicker-canvas-bearing,
.clockpicker-canvas-fg
  stroke: none
  fill: rgb(0, 149, 221)

.clockpicker-canvas-bg
  stroke: none
  fill: rgb(192, 229, 247)

.clockpicker-canvas-bg-trans
  fill: rgba(0, 149, 221, 0.25)

.clockpicker-canvas line
  stroke: rgb(0, 149, 221)
  stroke-width: 1
  stroke-linecap: round

  // shape-rendering: crispEdges;

.clockpicker-button.am-button
  margin: 1px
  padding: 5px
  border: 1px solid rgba(0, 0, 0, 0.2)
  border-radius: 4px

.clockpicker-button.pm-button
  margin: 1px 1px 1px 136px
  padding: 5px
  border: 1px solid rgba(0, 0, 0, 0.2)
  border-radius: 4px


//
// Date Picker
// --------------------------------------------------


.field-datepicker

.pika-single
  border: $popup-border !important
  background: white
  border-radius: 8px
  font-size: 12px
  margin-top: 5px
  z-index: $zindex-datepicker
  font-family: $font-family-sans-serif
  width: 310px

  &.is-bound
    box-shadow: $box-shadow-z2

  abbr
    border-bottom: none

  .pika-button
    border-radius: 999px !important
    text-align: center
    width: 29px
    height: 29px
    margin: 3px
    background: transparent

  .is-today .pika-button
    background: #ddd
    font-weight: bold
    color: $color-datepicker-today-text

  .is-selected .pika-button
    box-shadow: none
    background: $color-datepicker-selected-bg

  .pika-button:hover
    color: $color-datepicker-hover-text !important
    background: $color-datepicker-hover-bg !important

  .pika-label
    font-weight: normal

  .pika-table th
    text-transform: uppercase
    font-weight: normal
    line-height: 30px
    font-size: $font-size-base - 3

  .pika-footer
    .pika-label
      font-size: $font-size-base - 3

.clockpicker-popover
  z-index: $zindex-datepicker
  box-shadow: $box-shadow-z2
  border-radius: 8px
  border: $popup-border !important

  .popover-title
    font-size: 20px
    font-weight: normal
    border: none

  &.top
    margin-top: -5px

  &.right
    margin-left: 5px

  &.bottom
    margin-top: 5px

  &.left
    margin-left: -5px

  > .arrow
    display: none

.clockpicker-plate
  border: none

.clockpicker-hours .clockpicker-tick
  font-size: 12px

  &.tick-inner
    font-size: 16px

.clockpicker-minutes .clockpicker-tick
  font-size: 16px
