

@font-face
  font-family: 'FontAwesome'
  src: url($icon-font-path + "/fontawesome-webfont.eot?v=" + $icon-font-version)
  src: url($icon-font-path + "/fontawesome-webfont.eot?#iefix&v=" + $icon-font-version) format("embedded-opentype"), url($icon-font-path + "/fontawesome-webfont.woff?v=" + $icon-font-version) format("woff"), url($icon-font-path + "/fontawesome-webfont.ttf?v=" + $icon-font-version) format("truetype"), url($icon-font-path + "/fontawesome-webfont.svg#fontawesomeregular?v=" + $icon-font-version) format("svg")
  font-weight: normal
  font-style: normal

@font-face
  font-family: 'octo-icon'
  src: url($icon-font-path + "/octo-icon.eot?symvb &v=" + $icon-font-version)
  src: url($icon-font-path + "/octo-icon.eot?symvb#iefix&v=" + $icon-font-version) format("embedded-opentype"), url($icon-font-path + "/octo-icon.ttf?symvb&v=" + $icon-font-version) format("truetype"), url($icon-font-path + "//octo-icon.woff?symvb&v=" + $icon-font-version) format("woff"), url($icon-font-path + "//octo-icon.svg?symvb#octo-icon&v=" + $icon-font-version) format("svg")
  font-weight: normal
  font-style: normal
  font-display: block

//
// Standard icons
//

[class^="icon-"],
[class*=" icon-"]
  +icon-FontAutumn

  display: inline
  width: auto
  height: auto
  line-height: normal
  vertical-align: baseline
  background-image: none
  background-position: 0% 0%
  background-repeat: repeat
  margin-top: 0

[class^="icon-"]:before,
[class*=" icon-"]:before
  text-decoration: inherit
  display: inline-block
  speak: none

[class^="icon-"],
[class*=" icon-"]
  &.pull-left
    margin-right: .3em

  &.pull-right
    margin-left: .3em

//
// Prefixed icons ("oc-")
//

[class^="oc-icon-"],
[class*=" oc-icon-"]
  &:before
    display: inline-block
    margin-right: 8px

    +icon-FontAutumn

    vertical-align: baseline

  &.empty:before
    margin-right: 0

//
// Icon Sizes
//

// Makes the font 33% larger relative to the icon container
.icon-lg
  font-size: (4em / 3)
  line-height: (3em / 4)
  vertical-align: -15%

.icon-2x
  font-size: 2em

.icon-3x
  font-size: 3em

.icon-4x
  font-size: 4em

.icon-5x
  font-size: 5em

.icon-rotate-90
  transform: rotate(90deg)